import {
  useFinanceAllDepositRefund,
  useFinanceDepositRefundExport,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import style from "../FinanceManager.module.css";
import FinanceDepositRefundedListCard from "./FinanceDepositRefundedListCard";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../Ui/Button/CreateButton";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
const FinanceDepositRefundedList = ({ fid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [financeDepositRefundExport] = useFinanceDepositRefundExport();

  const { financeAllDepositRefund, financeAllDepositRefundLoading } =
    useFinanceAllDepositRefund({
      data: {
        fid: fid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !fid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllDepositRefund?.list?.length) {
      let obj = nextPage(financeAllDepositRefund?.list?.length);
      setState(() => obj);
    }
  }, [financeAllDepositRefund?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExport = () => {
    if (fid) {
      setDisabled((pre) => !pre);
      financeDepositRefundExport({
        fid: fid,
      })
        .then((res) => {
          if (res?.data?.data) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${pdfCdnUrl}/${res?.data?.data}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBlock: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
        <CreateButton
          label="export"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onExport}
        />
      </div>

      {financeAllDepositRefund?.list?.map((refund, index) =>
        financeAllDepositRefund?.list?.length === index + 1 ? (
          <div key={refund?._id} ref={ref}>
            <FinanceDepositRefundedListCard refund={refund} />
          </div>
        ) : (
          <FinanceDepositRefundedListCard refund={refund} key={refund?._id} />
        )
      )}
      {financeAllDepositRefundLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeAllDepositRefund?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No student found related this search."
          />
        )
      ) : (
        financeAllDepositRefund?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No students refunded."
          />
        )
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default FinanceDepositRefundedList;
